@font-face {
  font-family: "futura";
  src: url("../fonts/futubk-webfont.woff");
  src: url("../fonts/futubk-webfont.woff2") format("woff2");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "futura";
  src: url("../fonts/futurabookbt-webfont.woff");
  src: url("../fonts/futurabookbt-webfont.woff2") format("woff2");
  font-style: normal;
  font-weight: 600;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  color: #000;
}

/* Global classes */

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
  clear: both;
}

.clear {
  clear: both;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  overflow: hidden;
  margin-top: -1px;
  margin-left: -1px;
  position: absolute;
  clip: rect(0, 0, 0, 0);
  border: none;
}

/* Atomic classes */

.d-n {
  display: none;
}

.d-b {
  display: block;
}

/* Global classes */

img {
  max-width: 100%;
  height: auto;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 34px;
  min-height: 100px;
}

.header .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .nav-logo {
  display: flex;
  font-size: 18px;
  text-decoration: none;
  color: black;
}

.header .nav-logo__img {
  min-width: 37px;
  min-height: 37px;
}

.header .nav-menu {
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  align-self: center;
}

.header .nav-menu__li {
  margin-right: 30px;
  font-size: 12px;
}

.header .nav-menu__li:nth-child(5) {
  margin-right: 90px;
}

.header .nav-menu__li_youtube {
  align-self: center;
  text-decoration: none;
  color: black;
  font-size: 14px;
  line-height: 21px;
}

.header .nav-menu__li_youtube:before {
  content: '';
  display: inline-block;
  background-image: url(../img/svg/Vector.svg);
  width: 21px;
  height: 16px;
  margin-right: 10px;
}

.first-screen {
  height: 100vh;
  margin-bottom: 95px;
}

.first-screen:before {
  content: '';
  position: absolute;
  z-index: -3;
  top: 0;
  left: 0;
  bottom: 4.5%;
  right: 79%;
  background: rgba(60, 193, 224, 0.6);
}

.first-screen .slogan {
  display: flex;
  justify-content: space-between;
  padding-top: 172px;
}

.first-screen .slogan__img {
  width: calc(50% - 20px);
  background-image: url('../img/svg/Ellipse.svg');
  width: 491px;
  height: 491px;
  margin-left: -46px;
  position: relative;
  z-index: 5;
}

.first-screen .slogan__img:before {
  content: '';
  position: absolute;
  z-index: 0;
  width: 322.5px;
  height: 579px;
  top: -19px;
  left: 41%;
  background-image: url('../img/svg/Ellipse1.svg');
  background-repeat: no-repeat;
}

.first-screen .slogan__img:after {
  content: '';
  position: absolute;
  z-index: 0;
  width: 322.5px;
  height: 580px;
  top: -49px;
  left: 42%;
  background-image: url('../img/svg/Ellipse3.svg');
}

.first-screen .slogan__img_front {
  margin-top: -120px;
  margin-left: 23px;
}

.first-screen .slogan__img_btn {
  position: absolute;
  top: 0;
  right: 50px;
  width: 45px;
  height: 45px;
  z-index: 30;
  cursor: pointer;
  border-radius: 50%;
  background-color: #FFFFFF;
  transition: 0.5s;
  box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.25);
}

.first-screen .slogan__img_btn:before {
  content: '';
  position: absolute;
  background-image: url('../img/svg/Polygon.svg');
  background-repeat: no-repeat;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 55%;
  width: 17px;
  height: 17px;
}

.first-screen .slogan__img_btn:hover {
  transform: scale(1.2);
}

.first-screen .slogan-discription {
  width: calc(50% - 20px);
}

.first-screen .slogan-discription__text {
  width: 631px;
  font-family: futura;
  font-size: 57px;
  font-weight: 800;
  line-height: 70px;
  color: #000000;
  margin-bottom: 35px;
}

.first-screen .slogan-discription__btn {
  font-family: futura;
  width: 187px;
  height: 58px;
  line-height: 58px;
  text-align: center;
  text-decoration: none;
  color: #FFFFFF;
  font-size: 18px;
  background-color: #000000;
  display: inline-block;
  margin-bottom: 121px;
}

.first-screen .slogan-discription__link_a {
  text-decoration: none;
  margin-right: 5px;
  display: inline-block;
}

.first-screen .slogan-discription__p {
  margin-top: 10px;
  margin-left: -459px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 145px;
  line-height: 170px;
  position: relative;
  z-index: -3;
  color: #E5E5E5;
}

.first-screen .slogan-discription__p:before {
  content: '';
  position: absolute;
  left: -600px;
  bottom: 0;
  right: -120px;
  top: 59%;
  background-color: white;
  padding-bottom: 125px;
}

.our-robots__h {
  text-align: center;
  font-size: 58px;
  line-height: 70px;
  color: #000000;
  margin-bottom: 100px;
}

.our-robots__ul {
  justify-content: flex-end;
  padding-bottom: 121px;
}

.our-robots__li {
  list-style: none;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(25% - 20px);
}

.our-robots__img {
  margin-bottom: 26px;
}

.our-robots__figcaption {
  text-align: center;
}

.our-robots__figcaption_h {
  font-size: 30px;
  line-height: 36px;
  margin-bottom: 15px;
}

.our-robots__figcaption_p {
  font-size: 12px;
  line-height: 12px;
}

@media screen and (max-width: 780px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .our-robots__li {
    width: calc(50% - 20px);
  }
}